* { margin: 0; padding: 0; box-sizing: border-box; }

body { font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif; background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); min-height: 100vh; }

.App { max-width: 1200px; margin: 0 auto; padding: 2rem; }

header { text-align: center; color: white; margin-bottom: 3rem; }
header h1 { font-size: 3rem; font-weight: 800; margin-bottom: 0.5rem; }
header p { font-size: 1.2rem; opacity: 0.9; }

.container { background: white; border-radius: 24px; box-shadow: 0 25px 50px rgba(0,0,0,0.15); padding: 3rem; }

.section { margin-bottom: 2.5rem; }
.label { display: block; font-weight: 600; color: #374151; margin-bottom: 0.75rem; font-size: 1.1rem; }

.search-input { width: 100%; padding: 1rem 1.5rem; border: 2px solid #e5e7eb; border-radius: 12px; font-size: 1.1rem; transition: all 0.2s; }
.search-input:focus { outline: none; border-color: #667eea; box-shadow: 0 0 0 3px rgba(102,126,234,0.1); }

.rides-section h2 { color: #1f2937; margin-bottom: 1.5rem; font-size: 1.8rem; display: flex; align-items: center; gap: 1rem; }
.rides-count { background: #667eea; color: white; padding: 0.25rem 0.75rem; border-radius: 999px; font-size: 0.9rem; font-weight: 600; }

.ride-card { background: #f8fafc; border: 1px solid #e2e8f0; border-radius: 16px; padding: 2rem; margin-bottom: 1.5rem; transition: all 0.2s; position: relative; overflow: hidden; }
.ride-card:hover { transform: translateY(-4px); box-shadow: 0 20px 40px rgba(0,0,0,0.1); border-color: #667eea; }
.ride-card::before { content: ''; position: absolute; top: 0; left: 0; right: 0; height: 4px; background: linear-gradient(90deg, #667eea, #764ba2); }

.ride-route { display: flex; align-items: center; gap: 1.5rem; margin-bottom: 1rem; }
.origin, .destination { font-size: 1.4rem; font-weight: 700; }
.origin { color: #059669; }
.destination { color: #dc2626; }
.arrow { font-size: 1.8rem; font-weight: 300; color: #6b7280; }

.ride-details { display: grid; grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); gap: 1rem; margin-bottom: 1.5rem; color: #4b5563; }
.ride-details div { display: flex; align-items: center; gap: 0.5rem; font-size: 1rem; }

.book-btn { width: 100%; padding: 1rem 2rem; background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); color: white; border: none; border-radius: 12px; font-size: 1.1rem; font-weight: 600; cursor: pointer; transition: all 0.2s; }
.book-btn:hover:not(:disabled) { transform: translateY(-2px); box-shadow: 0 10px 25px rgba(102,126,234,0.3); }
.book-btn:disabled { opacity: 0.6; cursor: not-allowed; }

.message { padding: 1rem 1.5rem; border-radius: 12px; margin-bottom: 2rem; font-weight: 500; }
.message.success { background: #dcfce7; color: #166534; border: 1px solid #bbf7d0; }
.message.error { background: #fef2f2; color: #dc2626; border: 1px solid #fecaca; }

.no-rides { text-align: center; color: #6b7280; font-size: 1.1rem; padding: 3rem; }

.destinations-section { margin-top: 3rem; padding-top: 2.5rem; border-top: 1px solid #e5e7eb; }
.destinations-grid { display: flex; flex-wrap: wrap; gap: 0.75rem; }
.destination-tag { background: #f3f4f6; padding: 0.5rem 1rem; border-radius: 999px; font-size: 0.9rem; color: #374151; }
